* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --hover-color: #00acdc;
  text-transform: uppercase;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 100px);
  height: 62px;
  margin: 0 50px;
  display: flex;
}

.logo-container {
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  flex-grow: 1;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.nav__list {
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  font-size: inherit;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__item:first-child .nav__link {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 36px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__list a {
  color: #000;
  font-size: inherit;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

@media (width <= 1200px) {
  .nav__list {
    justify-content: space-around;
  }
}

@media (width <= 1024px) {
  .nav__list {
    justify-content: space-evenly;
  }
}
